@import url('https://fonts.googleapis.com/css?family=Amita');

.cover {
  font-family: 'Amita', cursive;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
  height: 400px;
  background-image: url('../images/lemon_cookies.jpg');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.cover .title {
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 15px;
  padding: 10px 20px;
  color: white;
  font-size: 3.5rem;
  text-shadow: -2px 0 rgb(227, 120, 8), 0 2px rgb(227, 120, 8), 2px 0 rgb(227, 120, 8), 0 -2px rgb(227, 120, 8);
}



